.resume-page {
	overflow-y: auto;
	max-height: 96%;
	transition: max-height 0.5s ease;
	scrollbar-width: none;
}

.resume-page-active {
	max-height: 55%;
}

.experience-block {
	display: flex;
	align-items: flex-start;
}

.left-column {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-right: 20px;
}

.book-div {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 45px;
	height: 45px;
	background: rgb(151 151 151 / 50%);
	border: 1px solid rgb(69, 69, 69);
	border-radius: 10px;
}

.book {
	width: 30px;
	height: 30px;
	user-select: none;
}

.line1 {
	width: 2px;
	height: 30px;
	flex-grow: 1;
	background-color: #626262;
}

.dot {
	width: 10px;
	height: 10px;
	background-color: #a2a2a2;
	border-radius: 50%;
	box-shadow: 0 0 0 4px rgb(45 45 45);
}

.line2 {
	width: 2px;
	height: 113px;
	flex-grow: 1;
	background-color: #626262;
}

.right-column {
	width: 100%;
}

.resume-title {
	margin: 5px 0px 25px 0px;
	font-size: 26px;
	font-weight: 500;
}

.experience-item {
	margin-bottom: 28px;
}

.experience-title {
	font-size: 20px;
	margin: 0px 0px 3px 0px;
	color: #e1e1e1;
}

.experience-year {
	font-size: 14px;
	margin: 0;
	color: #898989;
}

.experience-cont {
	font-size: 16px;
	margin: 5px 0 0 0;
	color: #dadada;
}

.resume-works-title {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 26px;
	font-weight: 500;
	margin: 0px 0px 8px 0px;
}

.projects {
	display: flex;
	flex-wrap: wrap;
	gap: 17px 33px;
}

.project-name {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 18px;
}

.project-div {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 363px;
	height: 185px;
	border-radius: 20px;
	background-color: rgba(39, 39, 39, 0.5);
	overflow: hidden;
	position: relative;
}

.project-image-div {
	display: flex;
	justify-content: center;
}

.project-div a {
	display: contents;
}

.project-div a:hover .project-image {
	transform: scale(1.17);
	transition: transform 0.3s ease;
}

.project-div a .project-image {
	transition: transform 0.3s ease;
}

.project-image {
	width: 90%;
	height: 85%;
	border-radius: 20px;
	user-select: none;
}

@media (max-width: 1365px) {
	.projects {
		justify-content: center;
	}

	.project-div {
		width: 450px;
		height: 210px;
	}

	.project-image {
		width: 100%;
	}

	.line2 {
		height: 140px;
	}
}

@media (max-width: 1115px) {
	.resume-title {
		font-size: 24px;
	}

	.experience-title {
		font-size: 18px;
	}

	.resume-works-title {
		font-size: 22px;
	}

	.line2 {
		height: 107px;
	}
}

@media (max-width: 988px) {
	.project-div {
		width: 400px;
		height: 190px;
	}

	.line2 {
		height: 153px;
	}
}

@media (max-width: 768px) {
	.resume-page {
		max-height: 98%;
	}

	.resume-page-active {
		max-height: 61%;
	}

	.line2 {
		height: 130px;
	}
}

@media (max-width: 700px) {
	.resume-page {
		max-height: 98%;
	}

	.resume-page-active {
		max-height: 61%;
	}

	.book-div {
		width: 40px;
		height: 40px;
	}

	.resume-title {
		font-size: 22px;
	}

	.line2 {
		height: 160px;
	}
}

@media (max-width: 576px) {
	.book-div {
		width: 35px;
		height: 35px;
	}

	.book {
		width: 25px;
		height: 25px;
	}

	.dot {
		width: 8px;
		height: 8px;
	}

	.resume-title {
		font-size: 20px;
	}

	.experience-title {
		font-size: 16px;
	}

	.experience-year {
		font-size: 13px;
	}

	.experience-cont {
		font-size: 15px;
	}

	.resume-works-title {
		font-size: 20px;
	}

	.project-name {
		font-size: 16px;
	}

	.project-div {
		width: 310px;
		height: 150px;
	}
}

@media (max-width: 435px) {
	.resume-page {
		max-height: 100%;
	}

	.resume-page-active {
		max-height: 63%;
	}

	.project-div {
		width: 280px;
		height: 130px;
	}
}

@media (max-width: 376px) {
	.left-column {
		margin-right: 15px;
	}

	.resume-title {
		font-size: 18px;
		margin: 5px 0px 15px 0px;
	}

	.experience-title {
		font-size: 14px;
	}

	.experience-year {
		font-size: 12px;
	}

	.experience-cont {
		font-size: 13px;
	}

	.resume-works-title {
		font-size: 18px;
	}

	.project-div {
		width: 230px;
		height: 110px;
	}
}
